export default {
	data() {
		return {
			mobile: '',
			code: '',
			state: false, //是否开启倒计时,
			time: 70, //总时间，单位秒
			getCodeTxt: '获取验证码',
			isBind:false,
			pickerAddress:'请选择省市区',
			goodsList:[],
			totalPrice: null,
		}
	},
	onLoad() {
		this.goodsList = uni.getStorageSync('order')
		this.countPrice()
	},
	methods: {
		
		//点击获取验证码
		getCode() {
			if (this.mobile == '') {
				this.showToast('请输入手机号码')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/UserCenter/sendBindActiveCode',
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					phone: this.mobile
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.showToast('验证码已发送')
						//开始倒计时
						this.state = true;
						//执行倒计时
						this.checkingTime();
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//倒计时
		checkingTime() {
			var _this = this
			var timer = setInterval(function() {
				_this.time--
				if (_this.time <= 0) {
					clearInterval(timer)
					_this.state = false;
					_this.getCodeTxt = '重新获取'
					_this.time = 70
				}
			}, 1000)
		},
		//绑定手机号
		bindMobile() {
			if (this.mobile == '') {
				this.showToast('输入手机号不能为空')
				return
			} else if (this.code == '') {
				this.showToast('验证码不能为空')
				return
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/bindUserPnone",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					phone: this.mobile,
					active_code: this.code
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.isBind = true
						uni.setStorageSync('mobile', this.mobile)
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		reduce(i) {
			if (this.goodsList[i].num == 1) {
				return
			}
			this.goodsList[i].num--
			this.countPrice()
		},
		add(i) {
			this.goodsList[i].num++
			this.countPrice()
		},
		//计算价格
		countPrice() {
			let totalPrice = 0
			for (let i = 0; i < this.goodsList.length; i++) {
				this.goodsList[i].shop_price = parseFloat(this.goodsList[i].shop_price).toFixed(2)
				totalPrice += parseFloat(this.goodsList[i].num) * parseFloat(this.goodsList[i].shop_price)
			}
			this.totalPrice = totalPrice.toFixed(2)
		}
	}
}
